@import "_variable.scss";

/***************************
        blog
****************************/
@charset "UTF-8";
.uta-blog{
    display: grid;
    margin-left: -10px;
    margin-right: -10px;
}
.uta-blog-item {
    -webkit-box-shadow: 0px 10px 30px 0px rgba(54, 56, 110, 0.15);
    box-shadow: 0px 10px 30px 0px rgba(54, 56, 110, 0.15);
    -webkit-transition: 0.3s;
    -o-transition: 0.3s;
    transition: 0.3s;
    border-radius: 6px;
    overflow: hidden;
    margin-left: 10px;
    margin-right: 10px;
    margin-bottom: 20px
}

.uta-blog-item-content {
    padding: 20px;

  ul {
    margin: 0;
    padding-left: 0;
    list-style: none;
    display: flex;
    align-items: center;
  }

}


ul.uta-blog-item-meta li{
  list-style:none;
}
.uta-blog-item-content ul li i {
    margin-right: 10px;
    font-size: 14px;
}

.uta-blog-item-img {
    position: relative;

  span{
    background: rgba(0, 0, 0, 0.50);
    -webkit-box-shadow: 0px 10px 30px 0px rgba(54, 56, 110, 0.15);
    box-shadow: 0px 10px 30px 0px rgba(54, 56, 110, 0.15);
    position: absolute;
    left: 10px;
    bottom: 10px;
    border: 1px solid #fff;
    border-radius: 20px;
    padding: 0 15px;
    font-size: 14px;
  }

}



.uta-blog-item-img span a {
    color: #fff;
}

.uta-blog-item-meta{
    font-size: 16px;
    border-top: 1px solid #eceef2;
    padding-top: 15px;
    margin-top: 15px;

 .avatar {
    border-radius: 50%;
    margin-right: 10px;
  }

  a {
    display: flex;
    align-items: center;
    padding-right: 10px;
  }


}



.uta-blog-item-meta .blog-meta-info {
    position: relative;
    margin: 0;
}

.uta-blog-item-meta .blog-meta-info:last-child {
    margin-right: 0;
}

.uta-blog-item-meta .blog-meta-info {
    position: relative;
    padding-right: 20px;
}

.uta-blog-item-meta .blog-meta-info:after {
    content: '';
    position: absolute;
    top: 8px;
    right: 16px;
    background: #b7b2b2;
    height: 16px;
    width: 1px;
}

.uta-blog-item-meta .blog-meta-info:last-child:after {
    display: none;
}

.uta-blog-item-content h4 {
   font-size: 20px;
    margin: 0;
    color: #333;
    padding-bottom: 20px;

  p{
      font-size: 16px
    }
  .blog-btn {
    margin-top: 20px;
    margin-bottom: 10px;
    font-size: 16px;
  }
}





.blog-item-date{
  margin: 0;
}
.front-date {
    font-weight: 600;
    margin: 10px 0 15px !important;
}
.uta-blog-item-style{
    overflow: hidden;
    position: relative;
    margin-bottom: 30px;
    margin-left: 10px;
    margin-right: 10px;
    box-sizing: border-box;
    overflow: hidden;
}
.uta-blog-item-style-1 .thumbnail{
  overflow: hidden;
}
.uta-blog-item-style-1 .thumbnail img{
  width: 100%;
  -webkit-transition:all .3s ease-in-out;
  transition: all .3s ease-in-out;
}
.uta-blog-item-style-1:hover .thumbnail img{
  transform: scale(1.1);
}
.uta-blog-item-style-1 .thumbnail .blog-date {
    position: absolute;
    top: 20px;
    left: 20px;
    background: yellow;
    width: 80px;
    height: 80px;
    border-radius: 50%;
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
}
.uta-blog-item-style-1 .thumbnail .blog-date span {
    font-size: 15px;
    font-weight: 600;
    padding: 10px;
    line-height: 20px;
    box-sizing: border-box;
}
ul{
  margin: 0 !important;
  padding: 0 !important;
  list-style: none;
}
.uta-blog-item-style.uta-blog-item-style-1 {
    box-shadow: 0px 0px 10px 0px #ccc;
}
.uta-blog-item-style.uta-blog-item-style-1 .content {
    padding-top: 15px;
    padding-bottom: 20px;
    padding-left: 10px;
    padding-right: 10px;
}
.uta-blog-item-style.uta-blog-item-style-1 .content ul li {
    display: inline-block;
    margin-right: 10px;
    color: #f38080;
    font-size: 16px;
}
.uta-blog-item-style.uta-blog-item-style-1 .content ul li:last-child{
  margin: 0px;
}
.uta-blog-item-style.uta-blog-item-style-1 .content ul li a{
  color: #f38080;
  font-size: 16px;
}

.uta-blog-item-style.uta-blog-item-style-1 .content h3{
  font-size: 18px;
    margin-top: 10px;
    margin-bottom: 14px;
    font-weight: 700;
}
.uta-blog-item-style.uta-blog-item-style-1 .content h3 a{
  color: #333;
}
.uta-blog-item-style.uta-blog-item-style-1 .content h3 a:hover{
  color: #f38080;
}
.uta-blog-item-style.uta-blog-item-style-1 .content p{
  font-size: 16px;
}
.uta-blog-item-style.uta-blog-item-style-1 .content .read-more{
  margin-top: 20px;
}
.uta-blog-item-style.uta-blog-item-style-1 .content .read-more a{
  font-size: 16px;
}
.uta-blog-item-style.uta-blog-item-style-1 .content .read-more a i{
  -webkit-transition:all .3s ease-in-out;
  transform: all .3s ease-in-out;
}
.uta-blog-item-style.uta-blog-item-style-1 .content .read-more a:hover{
  color: #f38080;
}
.uta-blog-item-style.uta-blog-item-style-1 .content .read-more a:hover i{
  padding-left: 7px;
}
a{
  -webkit-transition:all .3s ease-in-out;
  transform: all .3s ease-in-out;
}

/*
 * Blog 2
*/
.uta-blog-item-style.uta-blog-item-style-2 {
    overflow: hidden;
    box-shadow: 0px 0px 10px 0px #cccccc;
}
.uta-blog-item-style.uta-blog-item-style-2 .thumbnail {
    position: relative;
    width: 100%;
    overflow: hidden;
}
.uta-blog-item-style.uta-blog-item-style-2 .thumbnail:after{
  content: "";
  clear: both;
  display: block;
  overflow: hidden;
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background:rgba(0,0,0,.3);
  opacity: 0;
  visibility: hidden;
  -webkit-transition:all .3s ease-in-out;
  transition: all .3s ease-in-out;
}
.uta-blog-item-style.uta-blog-item-style-2:hover .thumbnail:after{
  opacity: 1;
  visibility: visible;
}
.uta-blog-item-style.uta-blog-item-style-2 .content {
    padding-top: 20px;
    padding-bottom: 20px;
    padding-left: 10px;
    padding-right: 10px;
}
